<template>
	<div>
		<div v-for="(question,index) in questions" :key="index">
			<h1>{{index}}.{{question}}</h1>
			<DictationCanvas :canvasId="`canvas`+index"></DictationCanvas>
			<h1 :id="`result`+index"></h1>
			<button @click="clear(index)" class="btn btn-info">Clear</button><br>
		</div>
		<button @click="saveImage()">제출</button>
	</div>
</template>

<script>
import DictationCanvas from "./DictationCanvas";
import axios from 'axios';

export default {
  name: 'DictationTest',
  components:{
		DictationCanvas
  },
  data() {
    return { 
        questions:[
					"민트색이 임포스터",
					"임포스터 아닙니다"
        ],
        API_ENDPOINT : 'https://inputtools.google.com/request?ime=handwriting&app=autodraw&dbg=1&cs=1&oe=UTF-8',
          preX: 0,
            preY: 0,
            curX:  0,
            curY : 0,
            pressedAt: 0,
            isPressing: false,
            maxX: 100000,
            minX: 0,
            maxY: 100000,
            minY: 0,
            shapes: [],
            curShape:[
           
           
           ],
            canvas:[]

    }
  }, created(){ 

  },mounted(){
            this.setCanvas();
     },
      methods:{
        clear(index){
            // console.dir(this.canvas[index]);
             window.__canvasList[index].clear();
             this.shapes[index]=[
               [],
               [],
               []
           ]
        },
        setCanvas(){
            this.clearCurShape();
            for(var i=0;i<this.questions.length;i++){
                this.canvas.push(document.getElementById(`canvas`+i));
                var arr=[];
                this.shapes.push(arr);
                // console.dir(window);
                // console.dir(this.canvas)
                this.setEventHandlers(i);
            }
        },
          onDrawingMouseDown(index,e){
            this.isPressing = true;
            this.pressedAt = Date.now();
            
            if(e.pointer.x < 0){
                e.pointer.x = 0;
            }
            if(e.pointer.y < 0){
                e.pointer.y = 0;
            }
            
            this.clearCurShape(); 
            this.curShape[index][0].push(e.pointer.x)
            this.curShape[index][1].push(e.pointer.y)
            this.curShape[index][2].push(Date.now() - this.pressedAt);
        
            // console.dir(e)

            // console.log('down')
        },
        onDrawingMouseMove(index,e){
            if(!this.isPressing) return;
            if(e.pointer.x < 0){
                e.pointer.x = 0;
            }
            if(e.pointer.y < 0){
                e.pointer.y = 0;
            }
            this.curShape[index][0].push(e.pointer.x)
            this.curShape[index][1].push(e.pointer.y)
            this.curShape[index][2].push(Date.now() - this.pressedAt);
            // console.log('move')

            (e.pointer.x > this.maxX) && (this.maxX = e.pointer.x);
            (e.pointer.x < this.minX) && (this.minX = e.pointer.x);
            (e.pointer.y > this.maxY) && (this.maxY = e.pointer.y);
            (e.pointer.y < this.minY) && (this.minY = e.pointer.y);
        },clearCurShape(){
            var arr=[
               [],
               [],
               []
           ]
           this.curShape=[];
           for(var i=0;i<this.questions.length;i++){
               this.curShape.push(arr);
           }
        },
        onDrawingMouseUp(index){
            
            this.isPressing = false;
            this.shapes[index].push(this.curShape[index]);
        }, setEventHandlers(index){
        
            window.__canvasList[index].on('mouse:down',this.onDrawingMouseDown.bind(null,index))
            window.__canvasList[index].on('mouse:move',this.onDrawingMouseMove.bind(null,index))
            window.__canvasList[index].on('mouse:up',this.onDrawingMouseUp.bind(null,index))
        }, getNoni(index){
            axios.post(this.API_ENDPOINT, {
                input_type : 0,
                requests: [{
                    ink:  this.shapes[index],
                    language : 'ko',
                    writing_guide: {
                        "width":this.canvas[index].width,
                        "height":this.canvas[index].height
                    }
                }]
            })
            .then(({data}) =>{
                // console.dir(data[1][0][1][0]);
                var result=data[1][0][1][0];
                document.getElementById("result"+index).innerText=result;
                
            })
        },makeImage(i){
                    var canvas = document.getElementById(`canvas`+i);
                    var imgDataUrl = canvas.toDataURL('image/png');
                    var blobBin = atob(imgDataUrl.split(',')[1]);	// base64 데이터 디코딩
                    var array = [];
                    for (var j = 0; j < blobBin.length; j++) {
                        array.push(blobBin.charCodeAt(j));
                    }
                    var file = new Blob([new Uint8Array(array)], {type: 'image/png'});	// Blob 생성
                    var formdata = new FormData();	// formData 생성
                     formdata.append("file", file,"file.png");
                    return formdata;
        },
       async saveImage(){
                for(var i=0;i<this.questions.length;i++){
                    await axios.post("http://localhost:9999/dictationsubmit/dictationsubmittest", this.makeImage(i))
                    .then(({data}) =>{
                            console.dir(data)
                             this.getNoni(i);
                    })
                }
        }

  }
    
}
</script>